<template>
  <div class="fixed-top w-100 h-100 d-flex align-items-center justify-content-center">
    <div class="text-center">
      <div class="spinner-border text-primary mb-3" role="status">
        <span class="visually-hidden"></span>
      </div>
      <div class="h4 text-primary">Initializing...</div>
    </div>
  </div>
</template>

<script setup>
import { onMounted } from "vue";
import { v4 as uuidv4 } from 'uuid';
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";

const store = useStore()
const router = useRouter()
const route = useRoute()

onMounted(()=>{
  let myPeerId = localStorage.getItem('myid')
  if(myPeerId == null){
    myPeerId = route.query.uuid
    myPeerId = (myPeerId == null) ? uuidv4() : myPeerId
    localStorage.setItem('myid', myPeerId)
  }  
  store.commit('setPeerId', myPeerId)
  router.replace('/')
})
</script>
